@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body{margin:0;background-color:#1f3756;color:#555;font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif}.none{display:none}.component_score .container_score{border:2px solid #bbb;border-radius:10px;display:flex;justify-content:space-between;margin:40px;padding:20px}.component_score .score{align-items:center;background-color:azure;border-radius:5px;display:flex;flex-direction:column;font-weight:bold;width:30%}.component_score .score p{margin:5px 0 0 0}.component_score .score strong{font-size:4em}.none{animation-name:aparicionRules;animation-duration:2s;animation-direction:alternate;display:none}.container_rules{animation-name:aparicionRules;animation-duration:2s;animation-direction:alternate}.container_rules .button_cerrar{position:absolute;background-color:white;border:none;top:12%;left:85%;color:#aaa;z-index:1}.container_rules img{background-color:white;padding:2em;border-radius:5%;position:absolute;top:10%;left:10%}.button_rules{background-color:#1f3756;border:.5px solid azure;border-radius:5%;box-shadow:0px 1px 1px 1px #555;color:azure;padding:10px;width:150px;text-shadow:1px 1px 2px #555;font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;margin-top:50px}@keyframes aparicionRules{to{transform:translatey(-500px)}from{transform:translateY(500px)}}footer{text-align:center}.container_opc{margin-top:100px;position:relative;animation-name:aparicion;animation-duration:2s}.container_opc .container_img-triangle{text-align:center}.container_opc .container_img-rock,.container_opc .container_img-paper,.container_opc .container_img-scissor{position:absolute;border-radius:50%}.container_opc .container_img-rock{align-items:center;background-color:azure;border:20px solid #cc1111;display:flex;justify-content:center;height:100px;left:35%;top:60%;width:100px}.container_opc .container_img-paper{align-items:center;background-color:azure;border:20px solid #3344ff;display:flex;height:100px;justify-content:center;top:-10%;left:10%;width:100px}.container_opc .container_img-scissor{align-items:center;background-color:azure;border:20px solid #ff9911;display:flex;height:100px;justify-content:center;top:-10%;left:65%;width:100px}@keyframes aparicion{to{opacity:1}from{opacity:0}}.container_run{display:flex;justify-content:space-around;margin-top:100px;padding:20px}.container_run .run_user .user{animation-name:aparicionUser;animation-duration:2s;align-items:center;background-color:azure;border:20px solid #11cc5f;border-radius:50%;display:flex;justify-content:center;height:80px;left:35%;top:60%;width:80px}.container_run .container_button{display:flex;flex-direction:column;justify-content:center}.container_run .container_button h1{position:relative;top:500px;animation-name:apacionButton;animation-delay:2s;animation-direction:reverse;animation-duration:2s;color:azure;width:100%}.container_run .container_button button{font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;color:#333;padding:10px;border-radius:10px}.container_run .run_machine{height:100px;width:100px}.container_run .run_machine .machine{animation-name:rotateMachine;animation-duration:2s;align-items:center;background-color:azure;border:20px solid #bc11cc;border-radius:50%;display:flex;justify-content:center;height:80px;left:35%;top:60%;width:80px}@keyframes rotateMachine{to{transform:rotate(0deg);opacity:1}from{opacity:0;transform:rotate(1500deg)}}@keyframes apacionButton{0%{opacity:0;transform:translateY(0px)}50%{opacity:1;transform:translateY(-500px)}100%{opacity:1;transform:translateY(-500px)}}@keyframes aparicionUser{to{opacity:1}from{opacity:0}}

